import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import AboutView from '../views/AboutView.vue'
import ContactView from '../views/ContactView.vue'
import CatalogoView from '../views/CatalogoView.vue'
import NotFound from '../views/NotFound.vue'
import EstiloPastel from '../views/EstiloPastel.vue'
import EstiloDark from '../views/EstiloDark.vue'
import EstiloMinimal from '../views/EstiloMinimal.vue'
import EstiloRomantico from '../views/EstiloRomantico.vue'
import EstiloModerno from '../views/EstiloModerno.vue'
import EstiloVintage from '../views/EstiloVintage.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/about',
    name: 'about',
    component: AboutView,
  },
  {
    path: '/contact',
    name: 'contact',
    component: ContactView,
  },
  {
    path: '/catalogo',
    name: 'catalogo',
    component: CatalogoView,
  },
  {
    path: '/pastel',
    name: 'pastel',
    component: EstiloPastel,
  },
  {
    path: '/dark',
    name: 'dark',
    component: EstiloDark,
  },
  {
    path: '/minimal',
    name: 'minimal',
    component: EstiloMinimal,
  },
  {
    path: '/romantico',
    name: 'romantico',
    component: EstiloRomantico,
  },
  {
    path: '/moderno',
    name: 'moderno',
    component: EstiloModerno,
  },
  {
    path: '/vintage',
    name: 'vintage',
    component: EstiloVintage,
  },
  {
    path: '*',
    component: NotFound
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
